import React from 'react'
import './Search.css'
import NavBar from '../NavBar/NavBar'
import {useEffect,useState} from 'react'
import axios from '../../axios'
import YouTube from 'react-youtube'
import {API_KEY,imgeUrl} from '../../constants/constants'
import {useLocation} from "react-router-dom";

function Search() {
    const [searchResult,setSearchResult]=useState([])
    const{state}=useLocation()
    const [urlId,setUrlid]=useState("");
    // const{Data}=state
    // console.log(state.Data)
    useEffect(()=>{
        axios.get(`search/movie?api_key=${API_KEY}&query=${state.Data}`).then(response=>{
            console.log(response.data.results[0]);
            if(response.data.results.length==0){
                alert("Not getting enough details");
            }else{
            setSearchResult(response.data.results)
            }
        })
    },[])
    const handleMovie=(id)=>{
        console.log(id)
        axios.get(`/movie/${id}/videos?api_key=${API_KEY}&language=en-US`).then(response=>{
          console.log(response.data.results[0])
          
          if(response.data.results.length!=0){
            setUrlid(response.data.results[0])
            console.log(response.data.results[0])
          }
          else{
            alert(`Sorry, Not Enough data found for ${""}`)
          }
        }).catch(error => {
          alert(`Sorry, Not Enough data found`)
        })
      }
      const opts = {
        height: '390',
        width: '100%',
        playerVars: {
          // https://developers.google.com/youtube/player_parameters
          autoplay: 1,
        },
      };



  return (
    <>
    <NavBar></NavBar>
    <div className='row'>
    <h2>We found Something for you:"{state.Data}"</h2>
    <div className='posters'>
    {searchResult.map((obj)=>
    
      
        <img src={obj.poster_path!=null ? imgeUrl+obj.poster_path:null} onClick={()=>handleMovie(obj.id)} className="poster" alt="Poster not found" />
      
    
   
    )}
     </div>
     {urlId &&  <YouTube opts={opts} videoId={urlId.key}/>}
</div>
</>
  )
}

export default Search