import React from 'react'
import './NavBar.css';
import {Firebase } from '../../firebase/Config'
import 'firebase/compat/auth';
// import {useState} from 'react'
import { useNavigate } from "react-router-dom";
function NavBar() {
  
    // const [data,setData]=useState()
    const history=useNavigate()
   

  const signout=()=>{
    Firebase.auth().signOut().then(() => {
      alert('Sign-out successful.');
      window.location.href='/signin';
    }).catch((error) => {
      console.log(error.Code,error.message);
    });
  }

  return (
    <div className='navbar'>
       <img className='logo' src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/1920px-Netflix_2015_logo.svg.png" alt="not found!" />
       <ul>
        <li><a href='home'>Home</a></li>
        <li><a href='#docu'>Documentries</a></li>
        <li>Romantic</li>
        <li>Tv Shows</li>
        <li>My Playlist</li>
       </ul>
       {/* <img className='avatar' src="https://i.pinimg.com/originals/0d/dc/ca/0ddccae723d85a703b798a5e682c23c1.png" alt="Not found" /> */}
       <input type="text" className='search'id="searchData" placeholder='Movie Name' />
       <button id='btn1' onClick={
        ()=>{
          const data=document.getElementById('searchData').value;
          // setData(data)
          window.location.href=`/search`;
          history('/search', { state: {Data:data} });

        }
       }>Search</button>
       <button id='btn' onClick={signout}>Sign Out</button>
    </div>
  )
}

export default NavBar