import axios from '../../axios'
import React, { useEffect, useState } from 'react'
import './Banner.css'
import {API_KEY,imgeUrl} from '../../constants/constants'
function Banner() {
  
  const [burlId,seturlId]=useState()
  const [movie,setMovie]=useState([])
  useEffect(()=>{
    axios.get(`trending/all/week?api_key=${API_KEY}&language=en-US`).then((response)=>{
      console.log(response.data.results[0])
      setMovie(response.data.results[Math.floor(Math.random() * 10)])
    })
  },[])
 const getMovieKey=(id)=>{
  axios.get(`/movie/${id}/videos?api_key=${API_KEY}&language=en-US`).then(response=>{
    console.log(response.data);
    if(response.data.results.length==0){
      alert("Not getting enough details");
    }else{
      seturlId(response.data.results[0].key)
      console.log(response.data.results[0].key)
      window.open(`https://www.youtube.com/watch?v=${response.data.results[0].key}`)
      
    }
  }).catch(err=>{
    alert("Records are empty!")
  })
 }
 const windows=(key)=>{
  window.open(`https://www.youtube.com/watch?v=${key}`)
 }
  return (

    <div 
    style={{backgroundImage:`url(${movie? imgeUrl+movie.backdrop_path:"Not found Image"})`} }
    className='banner'>
        <div className='content'>
            <h1 className='title'>{movie ? movie.title:"Not found"}</h1>
            <div className='banner_buttons'>
               <button className='button' onClick={()=>{
                  getMovieKey(movie.id)
                  console.log(burlId)}
              } >Play</button>
               <button className='button'>My list</button>
            </div>
            <h1 className='discription'>{movie ? movie.overview:"Not found"}</h1>

        </div>
        <div className='fade_bottom'></div>
    </div>
  )
}
export default Banner